import { AUTH_HEADER, ENDPOINT } from '@mintlify/http-client';
import { NextRequest } from 'next/server';

import { HOST_NAME } from '@/env';

export const dynamic = 'force-static';

export async function GET(
  req: NextRequest,
  { params }: { params: Promise<{ subdomain: string; slug?: string[] }> }
) {
  const { slug, subdomain } = await params;
  const providedHost = req.nextUrl.host;
  const trimmedHost = providedHost.replace('.' + HOST_NAME, '');

  if (!subdomain) {
    return Response.json(
      { error: 'Host not provided' },
      {
        status: 400,
      }
    );
  }

  // Prevent serving information across tenants
  if (trimmedHost !== subdomain) {
    return new Response(null, { status: 404 });
  }

  try {
    const path = slug?.join('/') ?? '/'; // if no path provided, assume we're fetching the index
    const authMetadataResponse = await fetch(
      `${ENDPOINT}/client/auth-metadata/${trimmedHost}/${encodeURIComponent(path)}`,
      {
        headers: AUTH_HEADER,
        next: {
          tags: [trimmedHost],
        },
      }
    );
    const authMetadata = await authMetadataResponse.json();
    return Response.json(authMetadata);
  } catch (err) {
    console.error(`Failed to fetch auth metadata`, err);
    return new Response(null, { status: 500 });
  }
}
